<template>
  <header class="login">
    <van-icon name="point-gift-o" class="orange mt-30 logo"></van-icon>
    <div class>
      <!--  -->
      <div class="handset ml-20 mr-20 flex jc-sb pb-5">
        <input v-model="user" class="f14" type="text" placeholder="" />
        <!-- <input class="gray f12 bg-fff" type="button" value=" " /> -->
      </div>
      <!--  -->
      <div class="handset ml-20 mr-20 flex jc-sb mt-20 pb-5">
        <input type="password" v-model="password" class="f14" placeholder="" />
      </div>

      <!--  -->
      <button @click="login" class="butt ml-20 mr-20 fff"></button>
    </div>
  </header>
</template>

<script>
import tim from '../../commen/tim/tim'
import { mapState } from "vuex";
import { Toast } from 'vant';
import { encrypt } from '@/utils/crypto.js'
export default {
  data() {
    return {
      user: "user2",
      password: "123456"
    };
  },
  computed: {
    ...mapState({
      isLogin: state => state.user.isLogin,
      isSDKReady: state => state.user.isSDKReady
    })
  },
  methods: {
    async login() {
      if (this.user && this.password) {
        //token
        const res = await this.$axios.post('/user/login',{
          username: this.user,
          password: encrypt(this.password)
        });
        const token = res.data;
        this.$store.commit('user/updateToken',token)
        const { data: userInfo } = await this.$axios.post('/user/getUserInfo',{token})
        // console.log('userInfo',userInfo)
        if (userInfo) {
          //userID  userSig tim
          this.tim
            .login({
              userID: userInfo.userId,
              userSig: userInfo.userSig
            })
            .then(res => {
              // console.log("tim", res);
              // 
              this.$store.commit("user/toggleIsLogin", true);
              //
              this.$store.commit("user/updateCurrentUserProfile", userInfo);
              //
              localStorage.setItem("userInfo", JSON.stringify(userInfo));
              //tim 
              localStorage.setItem("userTIMInfo", JSON.stringify(res.data));
              this.$router.push('/')
            })
            .catch(async err => {
              console.log("login error:", err); // 
              if(err.code === 70001){
                // UserSig 
                const { data: userInfo } = await this.$axios.post('user/userSig')
                this.tim.login({
                  userID: userInfo.userId,
                  userSig: userInfo.userSig
                }).then(res =>{
                  // 
                  this.$store.commit("user/toggleIsLogin", true);
                  //
                  this.$store.commit("user/updateCurrentUserProfile", userInfo);
                  //
                  localStorage.setItem("userInfo", JSON.stringify(userInfo));
                  //tim 
                  localStorage.setItem("userTIMInfo", JSON.stringify(res.data));
                  this.$router.push('/')
                })
              }
            });
        } else {
          Toast.fail('');
        }
      } else {
        Toast.fail('');
      }
    }
  }
};
</script>

<style lang="less" scoped>
@import "./login.less";
.logo {
  font-size: 100px !important;
  margin-bottom: 150px;
}
</style>

